import React, { useState, useRef } from 'react';
import Particles from 'react-tsparticles';
import { loadSlim } from 'tsparticles-slim';
import { ShieldCheckIcon, ChartBarIcon, Cog6ToothIcon, ClockIcon, SparklesIcon, CheckCircleIcon, ArrowUpTrayIcon, Squares2X2Icon, ShoppingBagIcon, ChatBubbleLeftRightIcon } from '@heroicons/react/24/outline';

const features = [
  {
    icon: <ShieldCheckIcon className="w-7 h-7 text-blue-400" />, title: 'Доверие',
    desc: 'Проверенные и устойчивые способы с высоким уровнем защиты информации'
  },
  {
    icon: <ChartBarIcon className="w-7 h-7 text-purple-400" />, title: 'Производительность',
    desc: 'Увеличение продуктивности и значительное уменьшение временных затрат'
  },
  {
    icon: <Cog6ToothIcon className="w-7 h-7 text-pink-400" />, title: 'Автономность',
    desc: 'Умные системы для автоматизации как простых, так и сложных задач.'
  },
  {
    icon: <ClockIcon className="w-7 h-7 text-cyan-400" />, title: 'Экономия времени',
    desc: 'Освобождение времени для важнейших направлений.'
  },
  {
    icon: <SparklesIcon className="w-7 h-7 text-yellow-400" />, title: 'Технологические разработки',
    desc: 'Современные технологии и регулярные обновления функционала.'
  },
  {
    icon: <CheckCircleIcon className="w-7 h-7 text-green-400" />, title: 'Служба поддержки',
    desc: 'Профессиональная техническая помощь и обучение сотрудников.'
  },
];

const solutions = [
  {
    icon: <ArrowUpTrayIcon className="w-12 h-12 mx-auto text-blue-400" />,
    title: 'Ethereum Uploader',
    desc: 'Автоматическая загрузка файлов в сеть Ethereum — быстро, безопасно, удобно.',
    link: 'https://t.me/EthUpl'
  },
  {
    icon: <Squares2X2Icon className="w-12 h-12 mx-auto text-purple-400" />,
    title: 'Уникализатор',
    desc: 'Комплексный софт для работы с видео и фото и уникализации их.',
    link: 'https://t.me/Ethereum_Reels_bot'
  },
  {
    icon: <ShoppingBagIcon className="w-12 h-12 mx-auto text-pink-400" />,
    title: 'Telegram Shop',
    desc: 'Всё необходимое для успешной работы с крипто-продуктами в одном месте.',
    link: 'https://t.me/Ethereum_Reels_bot'
  },
];

const privacyText = `
Политика конфиденциальности
--------------------------

Ваши данные защищены и не передаются третьим лицам. Мы используем только необходимые для работы сервиса данные и не храним лишнего. Подробнее о том, как мы защищаем вашу информацию, вы можете узнать, связавшись с нашей поддержкой.
`;

const termsText = `
Условия использования
---------------------

Используя наш сервис, вы соглашаетесь с тем, что не будете использовать его для незаконных целей. Все действия в сервисе осуществляются на ваш страх и риск. Мы не несем ответственности за действия пользователей.
`;

function App() {
  const [modal, setModal] = useState(null); // 'privacy' | 'terms' | null
  const featuresRef = useRef(null);
  const particlesInit = async (engine) => {
    await loadSlim(engine);
  };

  const scrollToFeatures = () => {
    featuresRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  return (
    <div className="min-h-screen bg-[#0a0a1a] relative overflow-x-hidden">
      {/* Космический фон */}
      <Particles
        id="tsparticles"
        init={particlesInit}
        options={{
          background: { color: { value: 'transparent' } },
          fpsLimit: 60,
          particles: {
            color: { value: '#a78bfa' },
            links: { color: '#a78bfa', distance: 120, enable: true, opacity: 0.2, width: 1 },
            move: { enable: true, speed: 0.3, random: true, outModes: { default: 'bounce' } },
            number: { value: 60, density: { enable: true, area: 800 } },
            opacity: { value: 0.3 },
            shape: { type: 'circle' },
            size: { value: { min: 1, max: 3 } },
          },
          detectRetina: true,
        }}
        className="absolute inset-0 z-0"
      />

      {/* Hero */}
      <section className="relative z-10 flex flex-col items-center justify-center min-h-screen text-center pt-24 pb-0">
        <h1 className="text-5xl md:text-7xl font-extrabold gradient-text mb-6 animate-fade-in">ETHEREUM UPLOADER</h1>
        <p className="text-xl md:text-2xl text-gray-300 mb-8 max-w-2xl mx-auto animate-fade-in delay-200">Современные решения для автоматизации ваших проектов. Быстро. Безопасно. Космически удобно.</p>
        <button className="btn btn-primary animate-float" onClick={scrollToFeatures}>Начать пользоваться</button>
      </section>

      {/* Слайд: Почему выбирают нас */}
      <section ref={featuresRef} className="relative z-10 min-h-screen flex flex-col justify-center items-center bg-[#0a0a1a] overflow-hidden animate-fade-in">
        {/* Космический фон для секции */}
        <Particles
          id="tsparticles-features"
          init={particlesInit}
          options={{
            background: { color: { value: 'transparent' } },
            fpsLimit: 60,
            particles: {
              color: { value: '#a78bfa' },
              links: { color: '#a78bfa', distance: 120, enable: true, opacity: 0.15, width: 1 },
              move: { enable: true, speed: 0.2, random: true, outModes: { default: 'bounce' } },
              number: { value: 40, density: { enable: true, area: 800 } },
              opacity: { value: 0.2 },
              shape: { type: 'circle' },
              size: { value: { min: 1, max: 2 } },
            },
            detectRetina: true,
          }}
          className="absolute inset-0 z-0 pointer-events-none"
        />
        <div className="container flex flex-col justify-center items-center h-full relative z-10">
          <h2 className="text-4xl font-bold text-center mb-4 gradient-text">Почему именно мы?</h2>
          <p className="text-lg text-gray-400 text-center mb-12">Автоматизация ваших процессов - наш ключ к успеху.</p>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8 w-full">
            {features.map((f, i) => (
              <div key={i} className="glass-card p-7 flex flex-col gap-3 items-start shadow-xl animate-fade-in" style={{animationDelay: `${i*100}ms`}}>
                {f.icon}
                <div className="font-bold text-lg mb-1">{f.title}</div>
                <div className="text-gray-400 text-sm">{f.desc}</div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Solutions */}
      <section className="relative z-10 py-24 flex flex-col justify-center items-center">
        <div className="container flex flex-col justify-center items-center">
          <h2 className="text-4xl font-bold text-center mb-4 gradient-text">Наши продукты</h2>
          <p className="text-lg text-gray-400 text-center mb-12">Уникальные в своем роде решения для вашего удобства</p>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-10 w-full items-stretch justify-items-center">
            {solutions.map((s, i) => (
              <div key={i} className="glass-card p-8 flex flex-col items-center text-center shadow-xl animate-fade-in h-full min-h-[320px] justify-between">
                {s.icon}
                <div className="font-extrabold text-xl mt-4 mb-2 uppercase">{s.title}</div>
                <div className="text-gray-400 mb-6 flex-1">{s.desc}</div>
                <a
                  href={s.link}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="btn btn-primary px-6 py-2 text-sm mt-auto"
                >
                  Подробнее
                </a>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Footer */}
      <footer className="relative z-10 pt-16 pb-8 bg-transparent">
        <div className="max-w-7xl mx-auto px-4 grid md:grid-cols-3 gap-10 text-gray-300 items-start text-center justify-center">
          <div>
            <div className="font-bold text-lg mb-2 gradient-text">ETHEREUM UPLOADER</div>
            <div className="mb-4 text-sm">Ethereum Uploader — сервис для безопасной загрузки файлов. Автоматизация, инновации, поддержка 24/7.</div>
            <div className="text-xs text-gray-500">© 2025 Ethereum Uploader. Все права защищены.</div>
          </div>
          <div>
            <div className="font-bold mb-2 gradient-text">Ресурсы</div>
            <ul className="space-y-2 text-sm">
            <li><a href="https://t.me/AetherReels" className="hover:underline">Все проекты</a></li>
              <li><a href="https://t.me/EthReviews" className="hover:underline">Отзывы</a></li>
              <li><a href="https://t.me/EthUpl" className="hover:underline">Приобрести продукт</a></li>
            </ul>
          </div>
          <div>
            <div className="font-bold mb-2 gradient-text">Контакты</div>
            <div className="flex items-center gap-2 mb-2 justify-center w-full"><ChatBubbleLeftRightIcon className="w-5 h-5 text-blue-400" /> <span>Техническая поддержка</span></div>
            <a href="https://t.me/EthUpl" target="_blank" rel="noopener noreferrer" className="inline-block mt-2 btn btn-primary px-4 py-2 text-sm">Написать в Telegram</a>
          </div>
        </div>
        <div className="text-center text-xs text-gray-600 mt-8">
          <button className="underline hover:text-blue-400 mr-2" onClick={() => setModal('privacy')}>Политика конфиденциальности</button>
          •
          <button className="underline hover:text-blue-400 ml-2" onClick={() => setModal('terms')}>Условия использования</button>
        </div>
      </footer>

      {/* Модальное окно для политики и условий */}
      {modal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/70">
          <div className="bg-[#181828] rounded-xl p-8 max-w-lg w-full text-left shadow-2xl relative animate-fade-in">
            <button className="absolute top-3 right-3 text-gray-400 hover:text-white text-2xl" onClick={() => setModal(null)}>&times;</button>
            <pre className="whitespace-pre-wrap text-gray-200 text-sm">
              {modal === 'privacy' ? privacyText : termsText}
            </pre>
          </div>
        </div>
      )}
    </div>
  );
}

export default App; 